<script lang="ts">import { getRouter } from '@curi/svelte';
import { createEventDispatcher } from 'svelte';
let _class = '';
export { _class as class };
export let params = {};
export let state = null;
export let href = undefined;
export let blank = false;
export let type = undefined;
export let label;
export let big = false;
export let active = false;
export let disabled = false;
export let waitOnDisabled = false;
export let secondary = false;
export let tertiary = false;
export let danger = false;
export let white = false;
$: primary = !secondary && !tertiary && !danger && !white;
export let customPadding = '';
const dispatch = createEventDispatcher();
let sizeClasses;
$: {
    sizeClasses = `
            ${customPadding || 'px-4 py-2'}
            text-sm
            border-2
            space-x-1
            rounded-md
            sm:leading-5
        `;
    if (big) {
        sizeClasses = `
                ${customPadding || 'px-8 py-3 md:px-10 md:py-4'}
                border-4
                leading-6
                space-x-3
                rounded-lg
                md:text-lg
            `;
    }
}
let colorClasses;
let hoverClasses;
let focusClasses;
let activeClasses;
let activatedClasses;
let disabledClasses;
$: {
    hoverClasses = '';
    focusClasses = '';
    activeClasses = '';
    activatedClasses = ``;
    disabledClasses = 'opacity-50 ' + (waitOnDisabled ? 'cursor-wait ' : '');
    if (primary) {
        colorClasses = `text-white bg-red-600`;
        hoverClasses += ` hover:bg-red-700`;
        focusClasses += ` focus-not-active:bg-red-500`;
        activeClasses += ` active:bg-red-600 active:border-red-500`;
        activatedClasses += ` bg-red-600 border-red-500`;
    }
    else if (secondary) {
        colorClasses = ``;
        hoverClasses += ` `;
        focusClasses += ` `;
        activeClasses += ``;
        activatedClasses += ``;
    }
    else if (tertiary) {
        colorClasses = `text-gray-400`;
        hoverClasses += ` hover:text-gray-300 hover:bg-gray-900`;
        focusClasses += ` focus-not-active:text-gray-300 focus-not-active:bg-gray-900`;
        activeClasses += ` active:text-gray-300 active:bg-black active:border-gray-900`;
        activatedClasses += ` bg-transparent text-gray-300 border-gray-900`;
    }
    else if (danger) {
        colorClasses = `text-red-700 bg-red-100`;
        hoverClasses += ` hover:bg-red-50`;
        focusClasses += ` focus-not-active:bg-red-50`;
        activeClasses += ` active:bg-red-100`;
        activatedClasses += ` bg-red-100`;
    }
    else if (white) {
        colorClasses = `text-pink-600 bg-white`;
        hoverClasses += ` hover:text-pink-500`;
        focusClasses += ` focus-not-active:text-pink-500`;
        activeClasses += ` active:text-pink-600`;
        activatedClasses += ` text-pink-600`;
    }
}
$: classes = `
        flex
        items-center
        justify-center
        font-medium
        select-none
        transition
        transform
        duration-150
        ease-in-out
        focus:outline-none
        ${colorClasses}
        ${sizeClasses}
        ${disabled
    ? disabledClasses
    : active
        ? activatedClasses
        : `border-transparent ${hoverClasses} ${focusClasses} ${activeClasses}`}
        ${_class}
    `;
let router = getRouter();
let canNavigate = (event, target) => {
    return (!event.defaultPrevented &&
        !target &&
        event.button === 0 &&
        !(event.metaKey || event.altKey || event.ctrlKey || event.shiftKey));
};
let url;
let target;
let handlePageLink;
$: {
    if (href && !href.startsWith('http')) {
        const split1 = href.split('#');
        const split2 = split1[0].split('?');
        const page = split2[0];
        const hash = split1[1];
        const query = split2[1];
        url = router.url({ name: page, params, hash, query });
        target = $$restProps.target;
        handlePageLink = (event) => {
            if (canNavigate(event, target)) {
                event.preventDefault();
                router.navigate({ url, state });
            }
        };
    }
}
</script>

{#if href}
  {#if handlePageLink}
    <a
      aria-label={label}
      title={label}
      href={url}
      {disabled}
      class={classes}
      on:click={handlePageLink}>
      <slot>Name</slot>
    </a>
  {:else}
    <a
      aria-label={label}
      title={label}
      {href}
      rel={blank === true ? 'noopener noreferrer' : ''}
      target={blank === true ? '_blank' : ''}
      {disabled}
      class={classes}>
      <slot>Name</slot>
    </a>
  {/if}
{:else if type}
  <button aria-label={label} title={label} {type} {disabled} class={classes}>
    <slot>Name</slot>
  </button>
{:else}
  <button
    on:click={() => {
      dispatch('click');
    }}
    aria-label={label}
    title={label}
    type="button"
    {disabled}
    class={classes}>
    <slot>Name</slot>
  </button>
{/if}
